// utils/videoProcessing.js
// utils/videoProcessing.js
import fs from 'fs';
import path from 'path';
import { spawn } from 'child_process';
import youtubedl from 'youtube-dl-exec';
import { fileURLToPath } from 'url';

// Get current directory
const __filename = fileURLToPath(import.meta.url);
const __dirname = path.dirname(__filename);

export async function downloadVideo(videoUrl, outputPath) {
    try {
        console.log('Starting video download:', videoUrl);

        // Ensure the temp directory exists
        const tempDir = path.dirname(outputPath);
        if (!fs.existsSync(tempDir)) {
            fs.mkdirSync(tempDir, { recursive: true });
        }

        // Download video using youtube-dl
        await youtubedl(videoUrl, {
            output: outputPath,
            format: 'best[ext=mp4]', // Force MP4 format
            noCheckCertificates: true,
            noWarnings: true,
            preferFreeFormats: true,
            addHeader: [
                'referer:youtube.com',
                'user-agent:Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/91.0.4472.124 Safari/537.36'
            ]
        });

        // Verify the file was downloaded
        if (!fs.existsSync(outputPath)) {
            throw new Error('Video file was not created');
        }

        const stats = fs.statSync(outputPath);
        if (stats.size === 0) {
            throw new Error('Downloaded file is empty');
        }

        console.log('Video downloaded successfully');
        return true;
    } catch (error) {
        console.error('Download error:', error);
        throw new Error(`Failed to download video: ${error.message}`);
    }
}

export async function transcribeVideo(videoPath) {
    console.log('Starting transcription for video:', videoPath);

    // Verify the video file exists and has content
    if (!fs.existsSync(videoPath)) {
        throw new Error('Video file not found');
    }

    const stats = fs.statSync(videoPath);
    if (stats.size === 0) {
        throw new Error('Video file is empty');
    }

    return new Promise((resolve, reject) => {
        const pythonScript = path.join(process.cwd(), 'transcribe_video.py');

        const pythonProcess = spawn('python', [pythonScript, videoPath], {
            env: {
                ...process.env,
                PYTHONIOENCODING: 'utf-8',
                PYTHONUNBUFFERED: '1'
            }
        });

        let transcriptData = '';
        let errorData = '';

        pythonProcess.stdout.on('data', (data) => {
            const output = data.toString();
            console.log('Transcription output:', output);
            transcriptData += output;
        });

        pythonProcess.stderr.on('data', (data) => {
            const error = data.toString();
            console.error('Transcription error:', error);
            errorData += error;
        });

        pythonProcess.on('error', (error) => {
            console.error('Failed to start Python process:', error);
            reject(error);
        });

        pythonProcess.on('close', (code) => {
            console.log(`Python process exited with code: ${code}`);

            if (code === 0 && transcriptData.trim()) {
                resolve(transcriptData.trim());
            } else {
                reject(new Error(`Transcription failed: ${errorData || 'No transcript generated'}`));
            }
        });
    });
}

export function cleanupTempFiles(filePath) {
    try {
        if (fs.existsSync(filePath)) {
            fs.unlinkSync(filePath);
            console.log('Cleaned up temp file:', filePath);
        }
    } catch (error) {
        console.error('Error cleaning up temp file:', error);
    }
}