// pages/api/transcribe.js
import { downloadVideo, transcribeVideo, cleanupTempFiles } from '../../utils/videoProcessing';
import path from 'path';
import fs from 'fs';

export const config = {
    api: {
        bodyParser: {
            sizeLimit: '500mb'
        },
        responseLimit: '500mb'
    }
};

export default async function handler(req, res) {
    if (req.method !== "POST") {
        return res.status(405).json({ error: "Method not allowed" });
    }

    let tempVideoPath = null;

    try {
        const { videoUrl } = req.body;

        if (!videoUrl) {
            return res.status(400).json({ error: "No video URL provided" });
        }

        console.log('Processing request for video URL:', videoUrl);

        // Ensure temp directory exists
        const tempDir = path.join(process.cwd(), 'temp');
        if (!fs.existsSync(tempDir)) {
            fs.mkdirSync(tempDir, { recursive: true });
        }

        // Create unique filename
        const tempFileName = `video-${Date.now()}.mp4`;
        tempVideoPath = path.join(tempDir, tempFileName);

        console.log('Starting video download to:', tempVideoPath);

        try {
            await downloadVideo(videoUrl, tempVideoPath);
        } catch (downloadError) {
            console.error('Download error:', downloadError);
            throw new Error(`Video download failed: ${downloadError.message}`);
        }

        // Verify the downloaded file
        if (!fs.existsSync(tempVideoPath)) {
            throw new Error('Video file was not created');
        }

        const fileStats = fs.statSync(tempVideoPath);
        if (fileStats.size === 0) {
            throw new Error('Downloaded video file is empty');
        }

        console.log('Video downloaded successfully, starting transcription...');

        try {
            const transcript = await transcribeVideo(tempVideoPath);

            if (!transcript || typeof transcript !== 'string' || transcript.trim().length === 0) {
                throw new Error('No valid transcript generated');
            }

            console.log('Transcription completed successfully');

            // Clean up the temp file
            cleanupTempFiles(tempVideoPath);

            return res.status(200).json({ transcript });
        } catch (transcribeError) {
            throw new Error(`Transcription failed: ${transcribeError.message}`);
        }

    } catch (error) {
        console.error('Error in transcribe API:', error);

        // Clean up temp file on error
        if (tempVideoPath && fs.existsSync(tempVideoPath)) {
            cleanupTempFiles(tempVideoPath);
        }

        return res.status(500).json({
            error: error.message,
            details: process.env.NODE_ENV === 'development' ? error.stack : undefined
        });
    }
}